/*-------------------------*/
/* $Globel */
/*-------------------------*/
*, *:before, *:after {
  position: relative;
  box-sizing: border-box; }

html, body {
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif', '微軟正黑體', 'Microsoft JhengHei';
  background: linear-gradient(0deg, #301A3B, #10030A); }

a {
  text-decoration: none; }
  a:hover, a:active, a:focus {
    text-decoration: none;
    outline: none; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

input, select, textarea {
  border: none; }
  input:focus, select:focus, textarea:focus {
    outline: none; }

input::-webkit-input-placeholder {
  color: #5C5C5C; }

img {
  max-width: 100%;
  height: auto; }

/*-------------------------*/
/* $Font */
/*-------------------------*/
/*-------------------------*/
/* $Layout */
/*-------------------------*/
/*-------------------------*/
/* $Text */
/*-------------------------*/
h1, h2, h3, h4, h5, h6 {
  margin: 0; }

/*-------------------------*/
/* $Button */
/*-------------------------*/
.btn {
  width: 100%;
  display: inline-block;
  vertical-align: top;
  position: relative;
  margin: 0;
  padding: 14px 0;
  box-sizing: border-box;
  font-size: 16px;
  font-family: 'Roboto', arial, sans-serif;
  letter-spacing: 0;
  text-decoration: none;
  border-radius: 8px;
  outline: 0;
  cursor: pointer;
  overflow: hidden;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  user-drag: none;
  transition: all 0.3s; }

/*-------------------------*/
/* $Title */
/*-------------------------*/
.title {
  margin-top: 90px;
  margin-bottom: 60px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center; }
  .title .title-img img {
    max-width: 180px; }
  .title h1 {
    font-size: 80px;
    color: #FFFFFF;
    text-shadow: 0px 3px 7px #0093FF; }
  .title h2 {
    font-size: 70px;
    color: #FFFFFF;
    text-shadow: 0px 3px 7px #0093FF; }

/*-------------------------*/
/* $Skill */
/*-------------------------*/
.skills {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 120px; }
  .skills .skill-container {
    margin-right: 90px; }
    .skills .skill-container:last-child {
      margin-right: 0; }
    .skills .skill-container .skill-row {
      display: flex;
      margin-bottom: 90px; }
      .skills .skill-container .skill-row:last-child {
        margin-bottom: 0; }
  .skills .skill {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 104px;
    height: 104px;
    line-height: 80px;
    font-size: 24px;
    color: #FFFFFF;
    border-radius: 40px;
    text-align: center;
    margin-right: 24px;
    cursor: pointer;
    box-shadow: 0px 3px 6px rgba(0, 147, 255, 0.44); }
    .skills .skill:last-child {
      margin-right: 0; }
    .skills .skill.init {
      background-color: rgba(0, 147, 255, 0.35);
      border: 8px solid rgba(255, 255, 255, 0.62); }
    .skills .skill.unlocked {
      background-color: rgba(0, 147, 255, 0.35);
      border: 8px solid rgba(92, 92, 92, 0.62); }
    .skills .skill.selected {
      background-color: rgba(0, 147, 255, 0.35);
      border: 8px solid rgba(87, 145, 255, 0.62); }
    .skills .skill span {
      font-size: 24px;
      position: absolute;
      white-space: nowrap;
      left: 50%;
      transform: translateX(-50%);
      bottom: -72px;
      text-shadow: 0px 1px 4px #0093FF; }

/*-------------------------*/
/* $Description */
/*-------------------------*/
.description {
  z-index: 1; }
  .description img {
    max-width: 200px; }
  .description .description-box {
    display: flex;
    max-width: 1200px;
    position: absolute;
    top: 0;
    left: 150px;
    background-color: #FFFFFF;
    border: 10px solid #0093FF;
    border-radius: 64px;
    padding: 60px;
    z-index: -1; }
    .description .description-box .description-img {
      margin-right: 60px; }
    .description .description-box .description-title {
      font-size: 36px;
      font-weight: 700;
      color: #505050;
      margin-bottom: 16px; }
    .description .description-box .description-text {
      font-size: 36px;
      color: #7B7B7B; }

/*-------------------------*/
/* $Footer */
/*-------------------------*/
.footer {
  height: 930px;
  overflow: hidden; }
  .footer .rocket {
    max-width: 260px;
    position: absolute;
    right: 300px;
    bottom: 0; }
